import { useState } from 'react';
import styles from './Faq.module.scss'
import arrowup from '../../assets/images/icons/arrowup.svg'
import arrowdown from '../../assets/images/icons/arrowdown.svg'
import list from '../../assets/images/icons/list.svg'

const Faq = () => {
  const [openItems, setOpenItems] = useState({});

  const faqData = [
    {
      id: 1,
      question: '   Parimatch  Steam?',
      answer: `<div class="${styles.paragraph}">,       Dota 2  ,      .</div>
      <div class="${styles.paragraph}">           <a class="${styles.link}" href="">parimatch.kz</a> </div>
      <div class="${styles.paragraph}">   ,     <a class="${styles.link}" href="">https://parimatch.kz/ru/regtel/3</a>      Parimatch. </div>
      <div class="${styles.paragraph}">         .     ID Parimatch  ,    . </div>
      <div class="${styles.paragraph}">  ID      - <a class="${styles.link}" href="">https://parimatch.kz/ru/my-account</a> </div>
      `
    },
    {
      id: 2,
      question: '   Steam?',
      answer: '       Parimatch,    Dota 2.     Steam,          !'
    },
    {
      id: 3,
      question: '       Steam?',
      answer: `<div class="${styles.paragraph}">,   ,  ,    Dota 2.       Parimatch.</div>
          PM Hero     .
      `
    },
    {
      id: 4,
      question: '  ?',
      answer: `<div class="${styles.paragraph}">  :</div>
      <div class="${styles.listItem}">
        <img src="${list}" />    ;
      </div>
      <div class="${styles.listItem}">
        <img src="${list}" />    ,   ;
      </div>
      <div class="${styles.listItem}">
        <img src="${list}" />           ;
      </div>
      <div class="${styles.listItem}">
        <img src="${list}" />        ;
      </div>
      <div class="${styles.listItem}">
        <img src="${list}" />     ,   Lenovo Legion Pro 7, ASUS TUF Gaming, Iphone 14 Pro Max, PlayStation 5, Apple Watch 8, Airpods 3 ,    5 000 000    FreeBet   100 000  ;
      </div>
      <div class="${styles.listItem}">
        <img src="${list}" />        ;
      </div>
         ,      .
      `
    },
    {
      id: 5,
      question: '  ?',
      answer: `  ,  21     ,    ,             .
      `
    },
  ];

  const toggleAccordion = id => {
    setOpenItems(prevState => ({
      ...prevState,
      [id]: !prevState[id]
    }));
  };

  return (
    <div className={`${styles.container} container-main`}>
      <div className={styles.title}>
        FAQ
      </div>
      {faqData.map((item, index) => (
        <div key={item.id} className={styles.item}>
          <div className={`${styles.question} ${openItems[item.id] ? `${styles.active}` : ''}`}
            onClick={() => toggleAccordion(item.id)}>

            <div>
              {item.question}
            </div>
            <div className="arrow-icon">
              {openItems[item.id] ?
                <img src={arrowup} alt="" /> :
                <img src={arrowdown} alt="" />}
            </div>
          </div>

          <div className={`${styles.answer} ${openItems[item.id] ? `${styles.open}` : ''}`}>
            <p dangerouslySetInnerHTML={{ __html: item.answer }}></p>
          </div>
        </div>
      ))}
    </div>
  )
}

export default Faq